function customQueryProducts(httpClient: HttpClient) {
  const router = createQueryOverloadRouter({
    builderQueryFunction: (options?: QueryProductsOptions) =>
      publicQueryProducts(httpClient)(options),
    typedQueryFunction: (
      query: V3ProductQuery,
      options?: QueryProductsOptions
    ) => publicTypedQueryProducts(httpClient)(query, options),
    hasOptionsParameter: true,
  });

  function overloadedQuery(
    query: V3ProductQuery,
    options?: QueryProductsOptions
  ): ReturnType<typeof universalTypedQueryProducts>;
  function overloadedQuery(
    options?: QueryProductsOptions
  ): ProductsQueryBuilder;
  function overloadedQuery(
    queryOrOptions?: V3ProductQuery | QueryProductsOptions,
    options?: QueryProductsOptions
  ): any {
    return router(...arguments);
  }

  return overloadedQuery;
}

